/**
 * 数据库性能监控增强器
 * 提供实时性能监控、异常检测、预警和自动优化建议
 * 
 * 功能:
 * - 实时性能指标收集和分析
 * - 异常检测和预警机制
 * - 性能趋势分析和预测
 * - 自动优化建议生成
 * - 性能报告和可视化数据
 * 
 * @author ZK-Agent Team
 * @version 1.0.0
 */

import { EventEmitter } from 'events'
import { databaseMonitor } from './monitoring'
import { connectionPoolAnalyzer } from './connection-pool-analyzer'
import { queryPerformanceOptimizer } from './query-performance-optimizer'

/**
 * 性能指标类型
 */
interface PerformanceMetrics {
  /** 时间戳 */
  timestamp: Date
  /** 连接池指标 */
  connectionPool: {
    active: number
    idle: number
    total: number
    usage: number
    waitingCount: number
    avgWaitTime: number
  }
  /** 查询性能指标 */
  queryPerformance: {
    totalQueries: number
    avgExecutionTime: number
    slowQueries: number
    errorRate: number
    throughput: number
  }
  /** 系统资源指标 */
  systemResources: {
    cpuUsage: number
    memoryUsage: number
    diskIO: number
    networkIO: number
  }
  /** 数据库指标 */
  database: {
    connections: number
    transactions: number
    locks: number
    deadlocks: number
    cacheHitRate: number
  }
}

/**
 * 异常类型
 */
enum AnomalyType {
  HIGH_LATENCY = 'high_latency',
  HIGH_ERROR_RATE = 'high_error_rate',
  CONNECTION_POOL_EXHAUSTION = 'connection_pool_exhaustion',
  MEMORY_LEAK = 'memory_leak',
  SLOW_QUERY = 'slow_query',
  DEADLOCK = 'deadlock',
  RESOURCE_CONTENTION = 'resource_contention'
}

/**
 * 异常检测结果
 */
interface AnomalyDetection {
  /** 异常ID */
  id: string
  /** 异常类型 */
  type: AnomalyType
  /** 严重程度 */
  severity: 'low' | 'medium' | 'high' | 'critical'
  /** 异常描述 */
  description: string
  /** 检测时间 */
  detectedAt: Date
  /** 相关指标 */
  metrics: Partial<PerformanceMetrics>
  /** 阈值信息 */
  threshold: {
    expected: number
    actual: number
    deviation: number
  }
  /** 建议操作 */
  recommendations: string[]
  /** 是否已解决 */
  resolved: boolean
  /** 解决时间 */
  resolvedAt?: Date
}

/**
 * 性能趋势
 */
interface PerformanceTrend {
  /** 指标名称 */
  metric: string
  /** 趋势方向 */
  direction: 'improving' | 'degrading' | 'stable'
  /** 变化率 */
  changeRate: number
  /** 预测值 */
  prediction: {
    nextHour: number
    nextDay: number
    confidence: number
  }
  /** 分析时间范围 */
  timeRange: {
    start: Date
    end: Date
  }
}

/**
 * 性能阈值配置
 */
interface PerformanceThresholds {
  /** 连接池阈值 */
  connectionPool: {
    usageWarning: number
    usageCritical: number
    waitTimeWarning: number
    waitTimeCritical: number
  }
  /** 查询性能阈值 */
  queryPerformance: {
    avgLatencyWarning: number
    avgLatencyCritical: number
    errorRateWarning: number
    errorRateCritical: number
    slowQueryThreshold: number
  }
  /** 系统资源阈值 */
  systemResources: {
    cpuWarning: number
    cpuCritical: number
    memoryWarning: number
    memoryCritical: number
  }
  /** 数据库阈值 */
  database: {
    connectionsWarning: number
    connectionsCritical: number
    cacheHitRateWarning: number
  }
}

/**
 * 监控配置
 */
interface MonitoringConfig {
  /** 是否启用 */
  enabled: boolean
  /** 采集间隔(ms) */
  collectionInterval: number
  /** 分析间隔(ms) */
  analysisInterval: number
  /** 历史数据保留时间(ms) */
  retentionPeriod: number
  /** 异常检测敏感度 */
  anomalyDetectionSensitivity: 'low' | 'medium' | 'high'
  /** 是否启用自动优化 */
  autoOptimization: boolean
  /** 报告生成间隔(ms) */
  reportInterval: number
}

/**
 * 性能报告
 */
interface PerformanceReport {
  /** 报告ID */
  id: string
  /** 生成时间 */
  generatedAt: Date
  /** 报告时间范围 */
  timeRange: {
    start: Date
    end: Date
  }
  /** 总体健康评分 */
  healthScore: number
  /** 关键指标摘要 */
  summary: {
    avgResponseTime: number
    errorRate: number
    throughput: number
    availability: number
  }
  /** 异常统计 */
  anomalies: {
    total: number
    byType: Record<AnomalyType, number>
    bySeverity: Record<string, number>
  }
  /** 性能趋势 */
  trends: PerformanceTrend[]
  /** 优化建议 */
  recommendations: {
    priority: 'high' | 'medium' | 'low'
    category: string
    description: string
    expectedImprovement: string
  }[]
  /** 详细数据 */
  detailedMetrics: PerformanceMetrics[]
}

/**
 * 数据库性能监控增强器类
 */
export class PerformanceMonitorEnhancer extends EventEmitter {
  private isMonitoring: boolean = false
  private collectionInterval: NodeJS.Timeout | null = null
  private analysisInterval: NodeJS.Timeout | null = null
  private reportInterval: NodeJS.Timeout | null = null
  private metricsHistory: PerformanceMetrics[] = []
  private anomalies: Map<string, AnomalyDetection> = new Map()
  private trends: Map<string, PerformanceTrend> = new Map()
  private config: MonitoringConfig
  private thresholds: PerformanceThresholds
  private lastHealthScore: number = 1.0
  private baselineMetrics: PerformanceMetrics | null = null

  constructor() {
    super()
    
    this.config = {
      enabled: true,
      collectionInterval: 30000, // 30秒
      analysisInterval: 300000,  // 5分钟
      retentionPeriod: 7 * 24 * 60 * 60 * 1000, // 7天
      anomalyDetectionSensitivity: 'medium',
      autoOptimization: false,
      reportInterval: 3600000 // 1小时
    }
    
    this.thresholds = {
      connectionPool: {
        usageWarning: 0.7,
        usageCritical: 0.9,
        waitTimeWarning: 100,
        waitTimeCritical: 500
      },
      queryPerformance: {
        avgLatencyWarning: 100,
        avgLatencyCritical: 500,
        errorRateWarning: 0.01,
        errorRateCritical: 0.05,
        slowQueryThreshold: 1000
      },
      systemResources: {
        cpuWarning: 0.7,
        cpuCritical: 0.9,
        memoryWarning: 0.8,
        memoryCritical: 0.95
      },
      database: {
        connectionsWarning: 80,
        connectionsCritical: 95,
        cacheHitRateWarning: 0.8
      }
    }
    
    this.setupEventListeners()
  }

  /**
   * 设置事件监听器
   */
  private setupEventListeners(): void {
    // 监听数据库监控事件
    databaseMonitor.on('metrics', (metrics) => {
      this.handleDatabaseMetrics(metrics)
    })
    
    // 监听连接池分析器事件
    connectionPoolAnalyzer.on('analysis-completed', (analysis) => {
      this.handlePoolAnalysis(analysis)
    })
    
    // 监听查询性能优化器事件
    queryPerformanceOptimizer.on('slow-query-detected', (query) => {
      this.handleSlowQuery(query)
    })
  }

  /**
   * 启动性能监控
   */
  async start(): Promise<void> {
    if (this.isMonitoring) {
      console.log('Performance monitor enhancer is already running')
      return
    }

    console.log('Starting performance monitor enhancer')
    
    this.isMonitoring = true
    
    // 建立基线指标
    await this.establishBaseline()
    
    // 启动指标收集
    if (this.config.enabled) {
      this.collectionInterval = setInterval(() => {
        this.collectMetrics()
      }, this.config.collectionInterval)
      
      // 启动分析
      this.analysisInterval = setInterval(() => {
        this.performAnalysis()
      }, this.config.analysisInterval)
      
      // 启动报告生成
      this.reportInterval = setInterval(() => {
        this.generateReport()
      }, this.config.reportInterval)
    }
    
    // 初始收集和分析
    await this.collectMetrics()
    await this.performAnalysis()
    
    this.emit('monitor-started')
    console.log('Performance monitor enhancer started successfully')
  }

  /**
   * 停止性能监控
   */
  async stop(): Promise<void> {
    if (!this.isMonitoring) {
      return
    }

    console.log('Stopping performance monitor enhancer')
    
    this.isMonitoring = false
    
    if (this.collectionInterval) {
      clearInterval(this.collectionInterval)
      this.collectionInterval = null
    }
    
    if (this.analysisInterval) {
      clearInterval(this.analysisInterval)
      this.analysisInterval = null
    }
    
    if (this.reportInterval) {
      clearInterval(this.reportInterval)
      this.reportInterval = null
    }
    
    this.emit('monitor-stopped')
  }

  /**
   * 建立基线指标
   */
  private async establishBaseline(): Promise<void> {
    console.log('Establishing performance baseline...')
    
    // 收集多个样本来建立基线
    const samples: PerformanceMetrics[] = []
    
    for (let i = 0; i < 5; i++) {
      const metrics = await this.collectCurrentMetrics()
      samples.push(metrics)
      
      if (i < 4) {
        await new Promise(resolve => setTimeout(resolve, 5000)) // 等待5秒
      }
    }
    
    // 计算基线指标
    this.baselineMetrics = this.calculateAverageMetrics(samples)
    
    console.log('Performance baseline established')
    this.emit('baseline-established', this.baselineMetrics)
  }

  /**
   * 收集性能指标
   */
  private async collectMetrics(): Promise<void> {
    try {
      const metrics = await this.collectCurrentMetrics()
      
      this.metricsHistory.push(metrics)
      
      // 清理过期数据
      this.cleanupOldMetrics()
      
      this.emit('metrics-collected', metrics)
      
    } catch (error) {
      console.error('Failed to collect metrics:', error)
      this.emit('collection-error', error)
    }
  }

  /**
   * 收集当前指标
   * 
   * @returns 当前性能指标
   */
  private async collectCurrentMetrics(): Promise<PerformanceMetrics> {
    const poolStats = connectionPoolAnalyzer.getStats()
    const queryStats = queryPerformanceOptimizer.getStats()
    const dbStats = databaseMonitor.getMetrics()
    
    return {
      timestamp: new Date(),
      connectionPool: {
        active: poolStats.activeConnections,
        idle: poolStats.idleConnections,
        total: poolStats.totalConnections,
        usage: poolStats.usage,
        waitingCount: poolStats.waitingCount,
        avgWaitTime: poolStats.avgWaitTime
      },
      queryPerformance: {
        totalQueries: queryStats.totalQueries,
        avgExecutionTime: queryStats.avgExecutionTime,
        slowQueries: queryStats.slowQueries,
        errorRate: queryStats.errorRate,
        throughput: queryStats.throughput
      },
      systemResources: {
        cpuUsage: this.getCPUUsage(),
        memoryUsage: this.getMemoryUsage(),
        diskIO: this.getDiskIO(),
        networkIO: this.getNetworkIO()
      },
      database: {
        connections: dbStats.connections || 0,
        transactions: dbStats.transactions || 0,
        locks: dbStats.locks || 0,
        deadlocks: dbStats.deadlocks || 0,
        cacheHitRate: dbStats.cacheHitRate || 0
      }
    }
  }

  /**
   * 获取CPU使用率
   * 
   * @returns CPU使用率
   */
  private getCPUUsage(): number {
    // 简化实现，实际应该使用系统API
    return Math.random() * 0.3 + 0.1 // 模拟10-40%的CPU使用率
  }

  /**
   * 获取内存使用率
   * 
   * @returns 内存使用率
   */
  private getMemoryUsage(): number {
    const used = process.memoryUsage()
    const total = used.heapTotal + used.external
    return Math.min(1, used.heapUsed / total)
  }

  /**
   * 获取磁盘IO
   * 
   * @returns 磁盘IO指标
   */
  private getDiskIO(): number {
    // 简化实现
    return Math.random() * 100
  }

  /**
   * 获取网络IO
   * 
   * @returns 网络IO指标
   */
  private getNetworkIO(): number {
    // 简化实现
    return Math.random() * 1000
  }

  /**
   * 清理过期指标
   */
  private cleanupOldMetrics(): void {
    const cutoffTime = new Date(Date.now() - this.config.retentionPeriod)
    
    this.metricsHistory = this.metricsHistory.filter(
      metrics => metrics.timestamp > cutoffTime
    )
    
    // 清理过期异常
    for (const [id, anomaly] of this.anomalies) {
      if (anomaly.detectedAt < cutoffTime) {
        this.anomalies.delete(id)
      }
    }
  }

  /**
   * 执行性能分析
   */
  private async performAnalysis(): Promise<void> {
    try {
      console.log('Performing performance analysis...')
      
      if (this.metricsHistory.length < 2) {
        console.log('Insufficient data for analysis')
        return
      }
      
      // 异常检测
      await this.detectAnomalies()
      
      // 趋势分析
      await this.analyzeTrends()
      
      // 计算健康评分
      const healthScore = this.calculateHealthScore()
      this.lastHealthScore = healthScore
      
      // 生成优化建议
      if (this.config.autoOptimization) {
        await this.generateOptimizationRecommendations()
      }
      
      this.emit('analysis-completed', {
        healthScore,
        anomalies: this.anomalies.size,
        trends: this.trends.size
      })
      
    } catch (error) {
      console.error('Performance analysis failed:', error)
      this.emit('analysis-error', error)
    }
  }

  /**
   * 异常检测
   */
  private async detectAnomalies(): Promise<void> {
    const currentMetrics = this.metricsHistory[this.metricsHistory.length - 1]
    const detectedAnomalies: AnomalyDetection[] = []
    
    // 检测连接池异常
    if (currentMetrics.connectionPool.usage > this.thresholds.connectionPool.usageCritical) {
      detectedAnomalies.push({
        id: `pool_exhaustion_${Date.now()}`,
        type: AnomalyType.CONNECTION_POOL_EXHAUSTION,
        severity: 'critical',
        description: `连接池使用率达到 ${(currentMetrics.connectionPool.usage * 100).toFixed(1)}%，超过临界阈值`,
        detectedAt: new Date(),
        metrics: { connectionPool: currentMetrics.connectionPool },
        threshold: {
          expected: this.thresholds.connectionPool.usageCritical,
          actual: currentMetrics.connectionPool.usage,
          deviation: currentMetrics.connectionPool.usage - this.thresholds.connectionPool.usageCritical
        },
        recommendations: [
          '增加连接池大小',
          '优化长时间运行的查询',
          '检查连接泄漏'
        ],
        resolved: false
      })
    }
    
    // 检测高延迟异常
    if (currentMetrics.queryPerformance.avgExecutionTime > this.thresholds.queryPerformance.avgLatencyCritical) {
      detectedAnomalies.push({
        id: `high_latency_${Date.now()}`,
        type: AnomalyType.HIGH_LATENCY,
        severity: 'high',
        description: `平均查询延迟为 ${currentMetrics.queryPerformance.avgExecutionTime.toFixed(1)}ms，超过临界阈值`,
        detectedAt: new Date(),
        metrics: { queryPerformance: currentMetrics.queryPerformance },
        threshold: {
          expected: this.thresholds.queryPerformance.avgLatencyCritical,
          actual: currentMetrics.queryPerformance.avgExecutionTime,
          deviation: currentMetrics.queryPerformance.avgExecutionTime - this.thresholds.queryPerformance.avgLatencyCritical
        },
        recommendations: [
          '优化慢查询',
          '添加数据库索引',
          '启用查询缓存'
        ],
        resolved: false
      })
    }
    
    // 检测高错误率异常
    if (currentMetrics.queryPerformance.errorRate > this.thresholds.queryPerformance.errorRateCritical) {
      detectedAnomalies.push({
        id: `high_error_rate_${Date.now()}`,
        type: AnomalyType.HIGH_ERROR_RATE,
        severity: 'critical',
        description: `查询错误率为 ${(currentMetrics.queryPerformance.errorRate * 100).toFixed(2)}%，超过临界阈值`,
        detectedAt: new Date(),
        metrics: { queryPerformance: currentMetrics.queryPerformance },
        threshold: {
          expected: this.thresholds.queryPerformance.errorRateCritical,
          actual: currentMetrics.queryPerformance.errorRate,
          deviation: currentMetrics.queryPerformance.errorRate - this.thresholds.queryPerformance.errorRateCritical
        },
        recommendations: [
          '检查数据库连接状态',
          '分析错误日志',
          '验证查询语法'
        ],
        resolved: false
      })
    }
    
    // 检测内存泄漏异常
    if (this.detectMemoryLeak()) {
      detectedAnomalies.push({
        id: `memory_leak_${Date.now()}`,
        type: AnomalyType.MEMORY_LEAK,
        severity: 'high',
        description: '检测到潜在的内存泄漏模式',
        detectedAt: new Date(),
        metrics: { systemResources: currentMetrics.systemResources },
        threshold: {
          expected: this.thresholds.systemResources.memoryWarning,
          actual: currentMetrics.systemResources.memoryUsage,
          deviation: currentMetrics.systemResources.memoryUsage - this.thresholds.systemResources.memoryWarning
        },
        recommendations: [
          '检查应用程序内存使用',
          '分析内存分配模式',
          '重启应用程序'
        ],
        resolved: false
      })
    }
    
    // 存储新检测到的异常
    detectedAnomalies.forEach(anomaly => {
      this.anomalies.set(anomaly.id, anomaly)
      this.emit('anomaly-detected', anomaly)
      
      // 发送告警
      this.sendAlert(anomaly)
    })
  }

  /**
   * 检测内存泄漏
   * 
   * @returns 是否检测到内存泄漏
   */
  private detectMemoryLeak(): boolean {
    if (this.metricsHistory.length < 10) {
      return false
    }
    
    const recent = this.metricsHistory.slice(-10)
    const memoryUsages = recent.map(m => m.systemResources.memoryUsage)
    
    // 检查内存使用是否持续增长
    let increasingCount = 0
    for (let i = 1; i < memoryUsages.length; i++) {
      if (memoryUsages[i] > memoryUsages[i - 1]) {
        increasingCount++
      }
    }
    
    // 如果80%的时间内存都在增长，可能存在内存泄漏
    return increasingCount >= 8
  }

  /**
   * 趋势分析
   */
  private async analyzeTrends(): Promise<void> {
    if (this.metricsHistory.length < 10) {
      return
    }
    
    const timeRange = {
      start: this.metricsHistory[0].timestamp,
      end: this.metricsHistory[this.metricsHistory.length - 1].timestamp
    }
    
    // 分析各项指标的趋势
    const metrics = [
      'connectionPool.usage',
      'queryPerformance.avgExecutionTime',
      'queryPerformance.errorRate',
      'systemResources.memoryUsage',
      'systemResources.cpuUsage'
    ]
    
    for (const metric of metrics) {
      const trend = this.calculateTrend(metric, timeRange)
      this.trends.set(metric, trend)
    }
  }

  /**
   * 计算指标趋势
   * 
   * @param metricPath - 指标路径
   * @param timeRange - 时间范围
   * @returns 趋势分析结果
   */
  private calculateTrend(metricPath: string, timeRange: { start: Date; end: Date }): PerformanceTrend {
    const values = this.metricsHistory.map(m => this.getNestedValue(m, metricPath))
    
    // 计算线性回归
    const n = values.length
    const x = Array.from({ length: n }, (_, i) => i)
    const y = values
    
    const sumX = x.reduce((a, b) => a + b, 0)
    const sumY = y.reduce((a, b) => a + b, 0)
    const sumXY = x.reduce((sum, xi, i) => sum + xi * y[i], 0)
    const sumXX = x.reduce((sum, xi) => sum + xi * xi, 0)
    
    const slope = (n * sumXY - sumX * sumY) / (n * sumXX - sumX * sumX)
    const intercept = (sumY - slope * sumX) / n
    
    // 计算R²
    const yMean = sumY / n
    const ssRes = y.reduce((sum, yi, i) => {
      const predicted = slope * x[i] + intercept
      return sum + Math.pow(yi - predicted, 2)
    }, 0)
    const ssTot = y.reduce((sum, yi) => sum + Math.pow(yi - yMean, 2), 0)
    const rSquared = 1 - (ssRes / ssTot)
    
    // 确定趋势方向
    let direction: 'improving' | 'degrading' | 'stable'
    const changeRate = Math.abs(slope)
    
    if (changeRate < 0.001) {
      direction = 'stable'
    } else {
      // 对于某些指标，增长是负面的（如错误率、延迟）
      const isNegativeMetric = metricPath.includes('errorRate') || 
                              metricPath.includes('avgExecutionTime') ||
                              metricPath.includes('usage')
      
      if (isNegativeMetric) {
        direction = slope > 0 ? 'degrading' : 'improving'
      } else {
        direction = slope > 0 ? 'improving' : 'degrading'
      }
    }
    
    // 预测未来值
    const nextHourIndex = n + (3600000 / this.config.collectionInterval) // 1小时后的索引
    const nextDayIndex = n + (24 * 3600000 / this.config.collectionInterval) // 1天后的索引
    
    return {
      metric: metricPath,
      direction,
      changeRate: slope,
      prediction: {
        nextHour: slope * nextHourIndex + intercept,
        nextDay: slope * nextDayIndex + intercept,
        confidence: Math.max(0, Math.min(1, rSquared))
      },
      timeRange
    }
  }

  /**
   * 获取嵌套对象值
   * 
   * @param obj - 对象
   * @param path - 路径
   * @returns 值
   */
  private getNestedValue(obj: any, path: string): number {
    return path.split('.').reduce((current, key) => current?.[key], obj) || 0
  }

  /**
   * 计算健康评分
   * 
   * @returns 健康评分(0-1)
   */
  private calculateHealthScore(): number {
    if (this.metricsHistory.length === 0) {
      return 1.0
    }
    
    const currentMetrics = this.metricsHistory[this.metricsHistory.length - 1]
    let score = 1.0
    
    // 连接池健康评分
    const poolUsage = currentMetrics.connectionPool.usage
    if (poolUsage > this.thresholds.connectionPool.usageCritical) {
      score -= 0.3
    } else if (poolUsage > this.thresholds.connectionPool.usageWarning) {
      score -= 0.1
    }
    
    // 查询性能健康评分
    const avgLatency = currentMetrics.queryPerformance.avgExecutionTime
    if (avgLatency > this.thresholds.queryPerformance.avgLatencyCritical) {
      score -= 0.25
    } else if (avgLatency > this.thresholds.queryPerformance.avgLatencyWarning) {
      score -= 0.1
    }
    
    // 错误率健康评分
    const errorRate = currentMetrics.queryPerformance.errorRate
    if (errorRate > this.thresholds.queryPerformance.errorRateCritical) {
      score -= 0.3
    } else if (errorRate > this.thresholds.queryPerformance.errorRateWarning) {
      score -= 0.15
    }
    
    // 系统资源健康评分
    const memoryUsage = currentMetrics.systemResources.memoryUsage
    if (memoryUsage > this.thresholds.systemResources.memoryCritical) {
      score -= 0.2
    } else if (memoryUsage > this.thresholds.systemResources.memoryWarning) {
      score -= 0.1
    }
    
    // 异常影响评分
    const criticalAnomalies = Array.from(this.anomalies.values())
      .filter(a => !a.resolved && a.severity === 'critical').length
    const highAnomalies = Array.from(this.anomalies.values())
      .filter(a => !a.resolved && a.severity === 'high').length
    
    score -= criticalAnomalies * 0.15
    score -= highAnomalies * 0.05
    
    return Math.max(0, Math.min(1, score))
  }

  /**
   * 生成优化建议
   */
  private async generateOptimizationRecommendations(): Promise<void> {
    const recommendations: any[] = []
    
    // 基于异常生成建议
    for (const anomaly of this.anomalies.values()) {
      if (!anomaly.resolved) {
        recommendations.push(...anomaly.recommendations.map(rec => ({
          priority: anomaly.severity === 'critical' ? 'high' : 'medium',
          category: 'anomaly_resolution',
          description: rec,
          expectedImprovement: '提升系统稳定性'
        })))
      }
    }
    
    // 基于趋势生成建议
    for (const trend of this.trends.values()) {
      if (trend.direction === 'degrading' && trend.prediction.confidence > 0.7) {
        recommendations.push({
          priority: 'medium',
          category: 'trend_prevention',
          description: `${trend.metric} 指标呈下降趋势，建议提前优化`,
          expectedImprovement: '防止性能进一步恶化'
        })
      }
    }
    
    this.emit('recommendations-generated', recommendations)
  }

  /**
   * 发送告警
   * 
   * @param anomaly - 异常信息
   */
  private sendAlert(anomaly: AnomalyDetection): void {
    console.warn(`🚨 Performance Alert: ${anomaly.description}`)
    
    this.emit('alert', {
      type: 'performance',
      severity: anomaly.severity,
      message: anomaly.description,
      anomaly
    })
  }

  /**
   * 生成性能报告
   */
  private async generateReport(): Promise<PerformanceReport> {
    const now = new Date()
    const oneHourAgo = new Date(now.getTime() - this.config.reportInterval)
    
    const reportMetrics = this.metricsHistory.filter(
      m => m.timestamp >= oneHourAgo
    )
    
    if (reportMetrics.length === 0) {
      throw new Error('No metrics available for report generation')
    }
    
    const avgMetrics = this.calculateAverageMetrics(reportMetrics)
    
    const report: PerformanceReport = {
      id: `report_${Date.now()}`,
      generatedAt: now,
      timeRange: {
        start: oneHourAgo,
        end: now
      },
      healthScore: this.lastHealthScore,
      summary: {
        avgResponseTime: avgMetrics.queryPerformance.avgExecutionTime,
        errorRate: avgMetrics.queryPerformance.errorRate,
        throughput: avgMetrics.queryPerformance.throughput,
        availability: this.calculateAvailability(reportMetrics)
      },
      anomalies: {
        total: this.anomalies.size,
        byType: this.getAnomaliesByType(),
        bySeverity: this.getAnomaliesBySeverity()
      },
      trends: Array.from(this.trends.values()),
      recommendations: await this.getRecommendations(),
      detailedMetrics: reportMetrics
    }
    
    this.emit('report-generated', report)
    return report
  }

  /**
   * 计算平均指标
   * 
   * @param metrics - 指标数组
   * @returns 平均指标
   */
  private calculateAverageMetrics(metrics: PerformanceMetrics[]): PerformanceMetrics {
    const count = metrics.length
    
    const avg = metrics.reduce((acc, m) => {
      acc.connectionPool.active += m.connectionPool.active
      acc.connectionPool.idle += m.connectionPool.idle
      acc.connectionPool.total += m.connectionPool.total
      acc.connectionPool.usage += m.connectionPool.usage
      acc.connectionPool.waitingCount += m.connectionPool.waitingCount
      acc.connectionPool.avgWaitTime += m.connectionPool.avgWaitTime
      
      acc.queryPerformance.totalQueries += m.queryPerformance.totalQueries
      acc.queryPerformance.avgExecutionTime += m.queryPerformance.avgExecutionTime
      acc.queryPerformance.slowQueries += m.queryPerformance.slowQueries
      acc.queryPerformance.errorRate += m.queryPerformance.errorRate
      acc.queryPerformance.throughput += m.queryPerformance.throughput
      
      acc.systemResources.cpuUsage += m.systemResources.cpuUsage
      acc.systemResources.memoryUsage += m.systemResources.memoryUsage
      acc.systemResources.diskIO += m.systemResources.diskIO
      acc.systemResources.networkIO += m.systemResources.networkIO
      
      acc.database.connections += m.database.connections
      acc.database.transactions += m.database.transactions
      acc.database.locks += m.database.locks
      acc.database.deadlocks += m.database.deadlocks
      acc.database.cacheHitRate += m.database.cacheHitRate
      
      return acc
    }, {
      timestamp: new Date(),
      connectionPool: { active: 0, idle: 0, total: 0, usage: 0, waitingCount: 0, avgWaitTime: 0 },
      queryPerformance: { totalQueries: 0, avgExecutionTime: 0, slowQueries: 0, errorRate: 0, throughput: 0 },
      systemResources: { cpuUsage: 0, memoryUsage: 0, diskIO: 0, networkIO: 0 },
      database: { connections: 0, transactions: 0, locks: 0, deadlocks: 0, cacheHitRate: 0 }
    })
    
    // 计算平均值
    Object.keys(avg).forEach(category => {
      if (category !== 'timestamp') {
        Object.keys(avg[category]).forEach(metric => {
          avg[category][metric] /= count
        })
      }
    })
    
    return avg as PerformanceMetrics
  }

  /**
   * 计算可用性
   * 
   * @param metrics - 指标数组
   * @returns 可用性百分比
   */
  private calculateAvailability(metrics: PerformanceMetrics[]): number {
    const totalSamples = metrics.length
    const healthySamples = metrics.filter(m => 
      m.queryPerformance.errorRate < this.thresholds.queryPerformance.errorRateWarning &&
      m.connectionPool.usage < this.thresholds.connectionPool.usageWarning
    ).length
    
    return healthySamples / totalSamples
  }

  /**
   * 按类型统计异常
   * 
   * @returns 异常类型统计
   */
  private getAnomaliesByType(): Record<AnomalyType, number> {
    const stats = {} as Record<AnomalyType, number>
    
    Object.values(AnomalyType).forEach(type => {
      stats[type] = 0
    })
    
    for (const anomaly of this.anomalies.values()) {
      if (!anomaly.resolved) {
        stats[anomaly.type]++
      }
    }
    
    return stats
  }

  /**
   * 按严重程度统计异常
   * 
   * @returns 异常严重程度统计
   */
  private getAnomaliesBySeverity(): Record<string, number> {
    const stats = {
      low: 0,
      medium: 0,
      high: 0,
      critical: 0
    }
    
    for (const anomaly of this.anomalies.values()) {
      if (!anomaly.resolved) {
        stats[anomaly.severity]++
      }
    }
    
    return stats
  }

  /**
   * 获取优化建议
   * 
   * @returns 优化建议列表
   */
  private async getRecommendations(): Promise<any[]> {
    const recommendations: any[] = []
    
    // 基于当前健康评分生成建议
    if (this.lastHealthScore < 0.7) {
      recommendations.push({
        priority: 'high',
        category: 'health_improvement',
        description: '系统健康评分较低，建议立即检查和优化',
        expectedImprovement: '提升整体系统健康度'
      })
    }
    
    return recommendations
  }

  /**
   * 处理数据库指标
   * 
   * @param metrics - 数据库指标
   */
  private handleDatabaseMetrics(metrics: any): void {
    // 实时检查关键指标
    if (metrics.connectionPoolUsage > 0.9) {
      this.emit('critical-alert', {
        type: 'connection_pool',
        message: 'Connection pool usage critical',
        value: metrics.connectionPoolUsage
      })
    }
  }

  /**
   * 处理连接池分析
   * 
   * @param analysis - 连接池分析结果
   */
  private handlePoolAnalysis(analysis: any): void {
    if (analysis.bottlenecks && analysis.bottlenecks.length > 0) {
      this.emit('bottleneck-detected', {
        type: 'connection_pool',
        bottlenecks: analysis.bottlenecks
      })
    }
  }

  /**
   * 处理慢查询
   * 
   * @param query - 慢查询信息
   */
  private handleSlowQuery(query: any): void {
    const anomaly: AnomalyDetection = {
      id: `slow_query_${Date.now()}`,
      type: AnomalyType.SLOW_QUERY,
      severity: query.executionTime > 5000 ? 'critical' : 'high',
      description: `检测到慢查询: ${query.executionTime}ms`,
      detectedAt: new Date(),
      metrics: {},
      threshold: {
        expected: this.thresholds.queryPerformance.slowQueryThreshold,
        actual: query.executionTime,
        deviation: query.executionTime - this.thresholds.queryPerformance.slowQueryThreshold
      },
      recommendations: [
        '优化查询语句',
        '添加适当的索引',
        '检查查询计划'
      ],
      resolved: false
    }
    
    this.anomalies.set(anomaly.id, anomaly)
    this.emit('anomaly-detected', anomaly)
  }

  /**
   * 获取当前状态
   * 
   * @returns 监控器状态
   */
  getStatus(): {
    isMonitoring: boolean
    healthScore: number
    metricsCount: number
    anomaliesCount: number
    trendsCount: number
    config: MonitoringConfig
  } {
    return {
      isMonitoring: this.isMonitoring,
      healthScore: this.lastHealthScore,
      metricsCount: this.metricsHistory.length,
      anomaliesCount: this.anomalies.size,
      trendsCount: this.trends.size,
      config: { ...this.config }
    }
  }

  /**
   * 获取最新指标
   * 
   * @returns 最新性能指标
   */
  getLatestMetrics(): PerformanceMetrics | null {
    return this.metricsHistory.length > 0 
      ? this.metricsHistory[this.metricsHistory.length - 1] 
      : null
  }

  /**
   * 获取异常列表
   * 
   * @returns 异常列表
   */
  getAnomalies(): AnomalyDetection[] {
    return Array.from(this.anomalies.values())
  }

  /**
   * 获取趋势列表
   * 
   * @returns 趋势列表
   */
  getTrends(): PerformanceTrend[] {
    return Array.from(this.trends.values())
  }

  /**
   * 更新配置
   * 
   * @param newConfig - 新配置
   */
  updateConfig(newConfig: Partial<MonitoringConfig>): void {
    Object.assign(this.config, newConfig)
    
    // 重启相关定时器
    if (this.isMonitoring) {
      this.stop().then(() => this.start())
    }
    
    console.log('Performance monitor configuration updated')
    this.emit('config-updated', this.config)
  }

  /**
   * 更新阈值
   * 
   * @param newThresholds - 新阈值
   */
  updateThresholds(newThresholds: Partial<PerformanceThresholds>): void {
    Object.assign(this.thresholds, newThresholds)
    
    console.log('Performance thresholds updated')
    this.emit('thresholds-updated', this.thresholds)
  }

  /**
   * 解决异常
   * 
   * @param anomalyId - 异常ID
   */
  resolveAnomaly(anomalyId: string): void {
    const anomaly = this.anomalies.get(anomalyId)
    if (anomaly) {
      anomaly.resolved = true
      anomaly.resolvedAt = new Date()
      
      console.log(`Anomaly resolved: ${anomaly.description}`)
      this.emit('anomaly-resolved', anomaly)
    }
  }

  /**
   * 手动触发分析
   */
  async triggerAnalysis(): Promise<void> {
    await this.performAnalysis()
  }

  /**
   * 手动生成报告
   */
  async triggerReport(): Promise<PerformanceReport> {
    return await this.generateReport()
  }
}

// 创建全局实例
export const performanceMonitorEnhancer = new PerformanceMonitorEnhancer()

// 导出类型
export type {
  PerformanceMetrics,
  AnomalyDetection,
  PerformanceTrend,
  PerformanceThresholds,
  MonitoringConfig,
  PerformanceReport
}

export { AnomalyType }
